.App {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    /* Gradient Red */

    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    /* Soft Shadow */

    box-shadow: 0px -0.11px 16.9495px rgba(183, 187, 225, 0.33);
    border-radius: 0px;
    transition: all 2s;
}
.App.active{
    background: rgba(0, 0, 0, 0.3);
}

.button {
    align-items: center;
    padding: 16px 32px;

    width: 198px;
    height: 56px;

    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    filter: drop-shadow(0px 0px 44px #CFDAE7);
    border-radius: 6px;
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    box-shadow: 0px 0px 24px rgba(234, 0, 41, 0.33);
    border-radius: 6px;
    transition: all 1s;
    color: white;

}
.modal-button{
    width: 100%;
    margin-top: 5%;
}
.button:hover{

    background: #FFFFFF;
    border-radius: 6px;
    color: black;
}
.modal{
    position: absolute;
    width: 552px;
    height: auto;
    padding: 2%;

opacity: 0;
    background: #FFFFFF;
    border-radius: 30px;
    transform: translateY(-150%);
    transition: all 1s;
}
.modal.active{
    opacity: 1;
    transform: translateY(0);
}
.title{
    font-weight: 500;
    font-size: 28px;
    line-height: 40px;
;
    color: #000000;
}
.description{
    font-family: Lab Grotesque;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    /* or 171% */

    padding-bottom: 10px;
    padding-top: 10px;

    color: #808080;
}
.text{

    font-weight: 500;
    color: #000000;
}
.result{
    color: #EA0029;;
}
.input{
    border: 1px solid #DFE3E6;
    box-sizing: border-box;
    border-radius: 3px;
    width: 100%;
    height: 35px;
}
.checkbox:before{
    width: 20px;
    height: 20px;
    color: black;
    background-color: black;
}
.showres{
    opacity: 0;
    transform: translateX(-100%);
    transition: all 1s;
}
.showres.active{
    opacity: 1;
    transform: translateX(0);

}
.switcher{
    padding: 10px;
    margin-left: 5%;
    width: 73px;
    height: 36px;
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    border-radius: 50px;
    transition: all 500ms;
}
.switcher.active{
    background: rgba(0, 0, 0, 0.3);

    transition: all 500ms;
}
.image{
    position: fixed;
    right: 5%;
    top: 5%;
    width: 20px;
}
@media (max-width: 767px) {
    .modal{
        justify-content: center;
        align-items: center;
        width: auto;
        height:100%;
        border-radius:0;

    }
    .title{
        padding-top: 30px;
    }
    .modal-button{
        position: absolute;
        width: 96%;
        bottom: 5%;

    }

}